body {
    font-size: 17px;
}

a {
    @apply font-semibold no-underline text-blue-600;

    &:hover {
        @apply text-blue-800;
    }
}

blockquote {
    @apply border-blue-400 border-l-4 font-normal italic my-8 pl-6 text-gray-800 text-lg;
}

code {
    @apply bg-gray-300 px-2 py-px rounded text-sm;
}

code.hljs {
    @apply bg-transparent p-0;

    .hljs-comment,
    .hljs-keyword,
    .hljs-meta {
        @apply font-normal not-italic;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    @apply leading-tight mb-4 mt-8 text-gray-900;

    &:first-child {
        @apply mt-0;
    }
}

h1 {
    @apply font-extrabold text-5xl;
}

h2 {
    @apply font-bold text-4xl;
}

h3 {
    @apply font-bold text-3xl;
}

h4 {
    @apply font-normal text-2xl;
}

h5 {
    @apply font-normal text-xl;
}

h6 {
    @apply font-light text-lg;
}

hr {
    @apply border-b border-blue-200 my-12 rounded-full;
}

li {
    ul,
    ol {
        @apply my-0;
    }
}

ol,
ul {
    @apply my-4 list-disc;
}

nav ul {
    @apply my-4 list-none;
}

ol {
    @apply list-decimal;
}

p {
    @apply my-3;

    @screen md {
        @apply my-6;
    }
}

pre {
    @apply bg-gray-200 leading-loose my-6 overflow-x-auto p-4 rounded shadow text-base;

    code {
        @apply bg-transparent block p-0;
    }
}

::selection {
    @apply bg-blue-500 text-white;
}
